@use 'utils';
@use 'responsive';
@use 'gridflex';

$background-color: rgba(228, 235, 232, 1);
$text-color: rgba(25, 26, 25, 1);
$border-color: rgba(3, 56, 64, 1);
$hover-color: rgba(159, 205, 212, 0.5);

html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

.wrapper-body {
  background-color: $background-color;
  color: $text-color;
  position: relative;
}
